import { useState, useEffect } from 'react';
import { ChakraProvider, Box, Container, Heading, Button, Flex } from '@chakra-ui/react';
import { extendTheme } from '@chakra-ui/react';
import Papa from 'papaparse';
import FilterSection from './components/FilterSection';
import CharacterGrid from './components/CharacterGrid';
import charactersData from './data/characters.csv';

//   
const characteristicsTranslation = {
  'Big-Eyed': ' ',
  'Canines': '',
  'Diving': '',
  'Egg-Laying': ' ',
  'Felines': '',
  'Fluffy': '',
  'Flying': '',
  'Horned': ' ',
  'Long Tailed': ' ',
  'Meat-Eating': '',
  'Plant-Eating': ''
};

//   
const nameTranslation = {
  'Bacon': '',
  'Barbie': '',
  'Bob': '',
  'Bruce': '',
  'Cabbage Dog': ' ',
  'Carrot': '',
  'Coco': '',
  'Curtis': '',
  'Dodo': '',
  'Dundun': '',
  'Fluffy': '',
  'Fubao': '',
  'Fuguee': '',
  'Garfat': '',
  'Googoo': '',
  'Gopher': '',
  'Hachi': '',
  'Hammer': '',
  'Harry': '',
  'Kato': '',
  'Kiko': '',
  'Kola': '',
  'Levi': '',
  'Lloyd': '',
  'Lotus': '',
  'Lou': '',
  'Macchiato': '',
  'Maneki': '',
  'Max': '',
  'Miu': '',
  'Moonmoon': '',
  'Morse': '',
  'Nemo': '',
  'Om Nom': '',
  'Ori': '',
  'Otta': '',
  'Pensky': '',
  'Ron': '',
  'Sam': '',
  'Shin': '',
  'Snow': '',
  'Sparky': '',
  'Spike': '',
  'Stella': '',
  'Sunday': '',
  'Sunny': '',
  'The Lamb': '',
  'Tiagra': '',
  'Tuskarr': '',
  'Underbite': '',
  'Uni': '',
  'Valiente': '',
  'Yurusa': ''
};

// Create a custom theme
const theme = extendTheme({
  fonts: {
    heading: `'Fredoka', sans-serif`,
    body: `'Fredoka', sans-serif`,
  },
  styles: {
    global: {
      body: {
        bg: 'orange.700',
        color: 'white'
      }
    }
  },
  components: {
    Box: {
      baseStyle: {
        borderRadius: 'xl'
      }
    },
    Card: {
      baseStyle: {
        bg: 'orange.500',
        color: 'white'
      }
    }
  }
});

//     
const PUBLIC_URL = process.env.PUBLIC_URL;

function App() {
  const [characters, setCharacters] = useState([]);
  const [filters, setFilters] = useState({});
  const [isKorean, setIsKorean] = useState(false);

  useEffect(() => {
    // Load and parse CSV file
    Papa.parse(charactersData, {
      download: true,
      header: true,
      complete: (results) => {
        setCharacters(results.data);
      }
    });
  }, []);

  const filteredCharacters = characters.filter(character => {
    return Object.entries(filters).every(([key, value]) => {
      if (!value) return true;
      return character[key] === 'TRUE';
    });
  });

  return (
    <ChakraProvider theme={theme}>
      <Box bg="orange.700" minH="100vh" py={8}>
        <Container maxW="container.xl">
          <Flex justify="space-between" align="center" mb={8}>
            <Heading textAlign="center" color="white">
              {isKorean ? '   ' : 'Party Animals Characters Filter'}
            </Heading>
            <Button
              onClick={() => setIsKorean(!isKorean)}
              colorScheme="orange"
              bg="white"
              color="orange.700"
              _hover={{ bg: 'orange.100' }}
              size="md"
              fontWeight="bold"
              px={6}
              boxShadow="md"
            >
              {isKorean ? 'ENG' : 'KOR'}
            </Button>
          </Flex>
          <Box bg="orange.500" p={6} borderRadius="xl" mb={6}>
            <FilterSection 
              filters={filters} 
              setFilters={setFilters} 
              isKorean={isKorean}
              translations={characteristicsTranslation}
            />
          </Box>
          <CharacterGrid 
            characters={filteredCharacters} 
            isKorean={isKorean}
            translations={characteristicsTranslation}
            nameTranslations={nameTranslation}
            imageBasePath={`${PUBLIC_URL}/images/`}
          />
        </Container>
      </Box>
    </ChakraProvider>
  );
}

export default App; 